}.Slide-Menu-Bar{
    position:fixed;
    width: 100%;
    height: 100%;
    background: rgb(12, 21, 22);
    z-index: 2;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    left: -50%;
    top: 50%;
    box-shadow: 2px -2px 7px 1px rgba(0,0,0,0.4);
    padding: 10px;
    color: #FFFFFF;
    margin: 0;  
    transition: all .34s ease-in;  
    visibility: collapse;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    overflow-y: scroll;
    -webkit-align-items: center;
            align-items: center;
}.Slide-Menu-Bar.active{
    left: 50%;
    transition: all .34s ease-out;
    visibility: visible;
}



