.tabs{
    display: -webkit-flex;
    display: flex;
    width: 50%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}.tabs > button{
    border: none;
    width: 100px;
    padding: 7px;
    border-radius: .3rem;
    margin: 12px;
    box-shadow: -1px 2px 6px 2px rgba(0,0,0,0.8);
}.tabs > button:hover{
    background: #057a6b;
    color: #FFF;
    transition: all .23s ease-in-out;
}
